.tabs {

}

.tabs__tablist {
	margin-top: -2rem;
	margin-bottom: 10.5rem;
	display: flex;
	justify-content: flex-end;

	@media (max-width: $laptop-md) {
		margin-top: -1rem;
		justify-content: flex-start;
		margin-bottom: 3rem;
	}

	@media (max-width: $tablet) {
		margin-bottom: 3.2rem;
	}
}

.tabs__panel {
	// display: none;
	max-height: 0.01rem;
	opacity: 0;
	pointer-events: none;
	transition: all 0.45s var(--tr-jolly-enter);
	transform: scale(0.7);
}
.tabs__panel.active {
	// display: block;
	max-height: 100rem;
	pointer-events: initial;
}

.tabs__panel.tab--animated {
	opacity: 1;
	transform: scale(1);
}

.tabs__button {
	// margin-left: 1.5rem;
	// margin-right: 1.5rem;

	@media (max-width: $tablet) {
		// margin-left: 0.5rem;
		// margin-right: 0.5rem;
	}
}

.tabs__button-icon-horizontal {
	height: 2.4rem;

	@media (max-width: $tablet) {
		height: 2rem;
	}
}
